.header {
  max-width: 880px;
  min-height: 74px;
  margin: 0 auto;
  padding: 45px 0 0;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  position: relative;
}

@media screen and (max-width:1024px) {
  .header {
    width: 90%;
  }
}

@media screen and (max-width:425px) {
  .header {
    min-height: 56px;
    padding-top: 28px;
  }
}

@media screen and (max-width:320px) {
  .header {
    width: 100%;
  }
}