.popup {
  width: 100%;
  min-height: 286px;
  display: flex;
  justify-content:center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s linear;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  z-index: 2;
}